.login{
    position: relative;
    height: 100vh;
    width: 100%;
    background: linear-gradient(
        to top,
        rgba(0,0,0,0.8),
        rgba(0,0,0,0.0),
        rgba(0,0,0,0.8)  
    ), url(https://assets.nflxext.com/ffe/siteui/vlv3/5a27cb25-33a9-4bcc-b441-95fefabcbd37/a296fd0c-d8d9-426a-901f-64ec0173632c/KR-ko-20210823-popsignuptwoweeks-perspective_alpha_website_medium.jpg);
    background-size: cover;
}
.login-logo{
    position:fixed;
    left:0;
    width:150px;
    padding-left: 20px;
    cursor: pointer;
    top: 20px;

}
.signup{
    position: absolute;
   top: 50%;
   left: 50%;
    max-width: 300px;
    padding: 70px;
    background-color: rgba(0,0,0,0.85);
    margin-left: auto;
    margin-right: auto;
    transform: translate(-50%,-50%);
}
.signup > form> h1 {
    color: #fff;
}
.signup > form {
    display: flex;
    flex-direction: column;
}
.signup > form > input {
    height: 30px;
    margin-top : 10px;
    margin-bottom: 10px;
    border-radius: 5px;
    padding:5px;
    border:none;
}
.signup > form > button {
    margin-top: 20px;
    padding: 16px 20px;
    font-size: 18px;
    color:#fff;
    background-color: #e50914;
    border-radius: 5px;
    border: none;
    cursor: pointer;
}

.signup-gray {
    color: gray;
}
.signup-link {
    color: white;
    margin-left: 5px;
    cursor:pointer;
}
.signup-link:hover {
    text-decoration: underline;
}
.signup-footer {
    margin-top: 5px;
}