.nav {
    position: fixed;
    top: 0;
    padding: 20px;
    width: 100%;
    height: 30px;
    z-index: 1;
    transition: all 0.5s;
}

.nav-contents{
    display: flex;
    justify-content:space-between;

}
.nav-logo{
    position: fixed;
    left: 0;
    top: 24px;
    width: 80px;
    cursor: pointer;
    padding-left: 20px;
}
.nav-avatar {
    position: fixed;
    top: 20px;
    right: 20px;
    width: 30px;
    cursor: pointer;
}
.nav-black {
    background-color: #111;
}